.about__title {
    @extend %title;
}

.about-card-set__img {
    margin: auto;
    margin-bottom: 20px
}

.about__container {

    @media screen and (min-width: 768px) {
        display: flex;
        justify-content: center;

    }
}

.about__description--container {
    @media screen and (min-width: 768px) {
        max-width: 392px;

        margin-left: 30px;
    }

}

.about__description,
.about__description--add {
    margin-bottom: 20px;
}